.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.board {
  position: absolute;
  float: left;
  height: 100%;
  width: 75%;
  background-color: #042c84;
}

.game-info {
  position: absolute;
  float: right;
  right: 0;
  height: 100%;
  width: 25%;
  min-width: 150px;
  background-color: #0086df;
}

.square {
  display: flex;
  float: left;
  align-items: center;
  justify-content: center;
  margin: 3px;
  font-size: 24px;
  font-weight: bold;
  line-height: 24px;
  min-height: 50px;
  height: 95%;
  aspect-ratio: 1/1;
  border-radius: 12px;
  background-color: #0086df;
  color: white;
  box-sizing: border-box;
  border: 7px solid #01143d;
  border-color: #01143d;
  user-select: none;
}

.square:hover {
  background-color: #32dee0;
  transition: 0.7s;
}


.board-row:after {
  clear: both;
  content: '';
  display: flex;
}

.game-board {
  border-collapse: collapse;
  margin: auto;
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translate(-75%, -50%);
  height: 75%;
  width: 100%;
  aspect-ratio: 1/1;
  white-space: nowrap;
}

.info {
  color: white;
  text-align: center;
  position: absolute;
  font-weight: bold;
  font-size: 24px;
  margin-top: -40px;
  top: 0;
  left: 50%;
  transform: translate(-50%, 0);
}

.hitbox {
  display: flex;
  float: left;
  align-items: center;
  justify-content: center;
/*  margin: 3px;*/
  font-size: 24px;
  font-weight: bold;
/*  line-height: 24px;*/
/*  min-height: 50px;*/
  height: 100%;
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 20px;
  background-color: #0086df;
  box-sizing: border-box;
  border: 7px solid #01143d;
  border-color: #01143d;
  user-select: none;
}

ol {
  position: absolute;
  margin: 100;
  top: 0;
  left: 0;
  transform: translate(0, 0);
  height: 75%;  
  white-space: nowrap;
}


